import CardHeader from "../../components/CardHeader";
import React, { useEffect, useState } from 'react';
import Input from '../../components/Input';
import HistoryNavButton from "./HistoryNavButton";
import { fetchReturns, fetchReturnsReal, fetchPurchaseReal, fetchPurchase, fetchSellsReal, fetchSells } from "../../api/Api";
import Return from '../returns/Return';
import { SyncLoader } from 'react-spinners';
import ReturnModal from '../returns/ReturnModal';
import HistoryItemModal from "./HistoryItemModal";
import HistoryItem from "./HistoryItem";
import toast, { Toaster } from 'react-hot-toast';
import { postData, fetchReturnById, updateReturnById } from '../../api/Api';
import '../../styles/Card.css';
import '../../styles/Cards/History.css';
import '../../styles/Components.css';

function History() {
    const [historyType, setHistoryType] = useState(0);
    const [loading, setLoading] = useState(true);
    const [sells, setSells] = useState([]);
    const [purchases, setPurchases] = useState([]);
    const [returns, setReturns] = useState([]);
    const [isReturnModalOpen, setIsReturnModalOpen] = useState(false);
    const [modalReturnData, setReturnModalData] = useState({});
    const [isSellModalOpen, setIsSellModalOpen] = useState(false);
    const [modalSellData, setSellModalData] = useState({});
    const [isPurchaseModalOpen, setIsPurchaseModalOpen] = useState(false);
    const [modalPurchaseData, setPurchaseModalData] = useState({});
    const [vin, setVin] = useState("");
    const [startDate, setStartDate] = useState("");
    const [endDate, setEndDate] = useState("");
    const [isAir, setIsAir] = useState(false);

    const toggleReturnModal = () => setIsReturnModalOpen(!isReturnModalOpen);
    const toggleSellModal = () => setIsSellModalOpen(!isSellModalOpen);
    const togglePurchaseModal = () => setIsPurchaseModalOpen(!isPurchaseModalOpen);

    const getCurrentType = () => historyType;
    const getFilter = () => {
        return {
            vin: vin,
            date_from: startDate,
            date_before: endDate,
        }
    }
    const handleApiResponse = (editedReturn, isNew, isAir, onSuccess, setLoading) => {
        const successMessage = isNew ? ' ' : ' ';

        toast.promise(
            isNew ? postData(editedReturn, isAir ? "returns/create_air_return" : "returns/create_return") :
                updateReturnById(editedReturn.id, editedReturn, isAir, setLoading),
            {
                loading: '',
                success: () => {
                    onSuccess();
                    return successMessage;
                },
                error: '-   ',
            }
        );
    };

    const loadData = async () => {
        setLoading(true); //  
        try {
            switch (historyType) {
                case 0:
                    // 
                    await fetchSellsReal(getFilter(), setSells, setLoading);
                    break;
                case 1:
                    // 
                    await fetchPurchaseReal(getFilter(), setPurchases, setLoading);
                    break;
                case 2:
                    // 
                    await fetchReturnsReal(getFilter(), setReturns, setLoading);
                    break;
                default:
                    break;
            }
        } catch (error) {
            console.error("  :", error);
            //   (   )
        } finally {
            setLoading(false); //  
        }
    };

    useEffect(() => {

        loadData();
    }, [historyType, vin]);

    const makeContent = () => {
        switch (historyType) {
            case 0:
                return (
                    <div className="HistoryItemsWrapper">
                        {
                            sells.length == 0 ?
                                (
                                    <div className="NumberDoesNotExist">
                                        <span>  <br />  </span>
                                    </div>
                                ) :
                                sells.map((el, index) => (
                                    <HistoryItem
                                        key={index}
                                        item={el}
                                        isSell={true}
                                        onClick={() => {
                                            setSellModalData(el);
                                            toggleSellModal();
                                        }}
                                    />
                                ))}
                        <div className='Space' />
                    </div>
                );
            case 1:
                return (
                    <div className="HistoryItemsWrapper">
                        {purchases.length == 0 ?
                            (
                                <div className="NumberDoesNotExist">
                                    <span>  <br />  </span>
                                </div>
                            ) : purchases.map((el, index) => (
                                <HistoryItem
                                    key={index}
                                    item={el}
                                    isSell={false} //   false,    
                                    onClick={() => {
                                        setPurchaseModalData(el);
                                        togglePurchaseModal();
                                    }}
                                />
                            ))}
                        <div className='Space' />
                    </div>
                );
            case 2:
                return (
                    <div className="HistoryItemsWrapper">
                        {returns.length == 0 ?
                            (
                                <div className="NumberDoesNotExist">
                                    <span>  <br />  </span>
                                </div>
                            ) : returns.map((el, index) => (
                                <Return
                                    key={index}
                                    returnData={el}
                                    onClick={() => {
                                        setIsAir(el.isAir);
                                        setReturnModalData(el);
                                        toggleReturnModal();
                                    }}
                                />
                            ))}
                        <div className='Space' />
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div className="History">
            <CardHeader label="" />
            <div className="HistoryDateWrapper">
                <div className="DateInputWrapper">
                    <label htmlFor="date_from" className="form-label"></label>
                    <Input id="date_from" isDynamic={true} parentText={startDate} setParentText={setStartDate} label="" hint=".." type="date" />
                </div>
                <div className="DateInputWrapper">
                    <label htmlFor="date_to" className="form-label"></label>
                    <Input id="date_to" label="" isDynamic={true} parentText={endDate} setParentText={setEndDate} hint=".." type="date" />
                </div>
            </div>
            <div className="HistoryVinInput">
                <Input hint=" " type="text" isSearch={true} isDynamic={true} setParentText={setVin} iconOnClick={loadData} />
            </div>
            <div className="HistoryNav">
                <HistoryNavButton label="" type={0} getCurrentType={getCurrentType} setHistoryType={setHistoryType} />
                <HistoryNavButton label="" type={1} getCurrentType={getCurrentType} setHistoryType={setHistoryType} />
                <HistoryNavButton label="" type={2} getCurrentType={getCurrentType} setHistoryType={setHistoryType} />
            </div>
            {loading ?
                <div className='LoaderWrapper'>
                    <SyncLoader color="#A7A7A7" />
                </div> :
                <div className="HistoryContent">
                    {makeContent()}
                </div>
            }
            <ReturnModal
                isOpen={isReturnModalOpen}
                onClose={toggleReturnModal}
                returnData={modalReturnData}
                isHistory={true}
                isCreating={false}
                isAir={isAir}
                loadReturns={loadData}
                handleApiResponse={handleApiResponse}
            />
            <HistoryItemModal
                isOpen={isSellModalOpen}
                onClose={toggleSellModal}
                itemData={modalSellData}
                isSell={true}
            />
            <HistoryItemModal
                isOpen={isPurchaseModalOpen}
                onClose={togglePurchaseModal}
                itemData={modalPurchaseData}
                isSell={false}
            />
        </div>
    );
}

export default History;
